@import '../../style/app.scss';
.footer {
  display: flex;
  flex-direction: column;
  flex-direction: column-reverse;
  gap: 14px;
  align-items: flex-end;
  padding: 16px;
  background: $dark100;
  &__copyright {
    font-size: 13px;
    line-height: 15px;
    color: $gray100;
  }
  &__phone {
    font-weight: 400;
    font-size: 13px;
    line-height: 15px;
    color: $green;
    &:hover,
    &:focus {
      color: $white;
    }
    &:disabled {
      color: $gray100;
    }
  }
}
@media (min-width: 590px) {
  .footer {
    flex-direction: row;
    justify-content: space-between;
    padding: 0px 32px 32px 0;
    background: transparent;
  }
  .footer__phone {
    color: $dark100;
  }
  .footer__phone:focus,
  .footer__phone:hover {
    color: $green;
  }
}
