@font-face {
  font-family: 'Roboto';
  src: url('fonts/Roboto-Bold.eot');
  src: url('fonts/Roboto-Bold.woff') format('woff'),
    url('fonts/Roboto-Bold.ttf') format('truetype'),
    url('fonts/Roboto-Bold.svg') format('svg');
  font-weight: 700;
  font-style: normal;
}
